import { AirplaneListPageModule } from './../pages/airplane-list/airplane-list.module';
import { TestPageModule } from './../pages/test/test.module';
import { ComponentsModule } from './../components/components.module';
import { GlobalModule } from './../global/global.module';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, IonicPageModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { RestangularModule } from 'ngx-restangular';
import { MyApp } from './app.component';
import { CalendarModule } from "ion2-calendar";
import { 
    CityListPageModule,
    HomePageModule,
    InquirePageModule,
    MainPageModule,
    OrderPageModule,
    PersonPageModule
} from './../pages/pages.index';
import { ThreeDeeTouch, ThreeDeeTouchQuickAction, ThreeDeeTouchForceTouch } from '@ionic-native/three-dee-touch';
@NgModule({
    declarations: [
        MyApp,
    ],
    imports: [
        BrowserModule,
        GlobalModule,
        TestPageModule,
        CalendarModule,
        IonicPageModule,
        ComponentsModule,
        CityListPageModule,
        HomePageModule,
        InquirePageModule,
        MainPageModule,
        OrderPageModule,
        PersonPageModule,
        AirplaneListPageModule,
        IonicModule.forRoot(MyApp, {
            backButtonText: '',
            iconMode: 'ios',
            modalEnter: 'modal-slide-in',
            modalLeave: 'modal-slide-out',
            tabsPlacement: 'bottom',
            pageTransition: 'ios-transition'
        }, 
{
  links: [
    { loadChildren: '../pages/airplane-list/airplane-list.module#AirplaneListPageModule', name: 'airplanelist', segment: 'airplane-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/test/test.module#TestPageModule', name: 'TestPage', segment: 'test', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/city-list/city-list.module#CityListPageModule', name: 'CityListPage', segment: 'city-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inquire/inquire.module#InquirePageModule', name: 'InquirePage', segment: 'inquire', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/main/main.module#MainPageModule', name: 'main', segment: 'main', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/order/order.module#OrderPageModule', name: 'OrderPage', segment: 'order', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/person/person.module#PersonPageModule', name: 'PersonPage', segment: 'person', priority: 'low', defaultHistory: [] }
  ]
}),
        // InquirePageModule.forRoot(),
        RestangularModule.forRoot((RestangularProvider) => {
            RestangularProvider.setBaseUrl('api');
            // RestangularProvider.setDefaultHeaders({'AuthoriAuzation': 'Bearer UDXPx-Xko0w4BRKajozCVy20X11MRZs1'});
        })
    ],
    bootstrap: [IonicApp],
    entryComponents: [
        MyApp,
    ],
    providers: [
        StatusBar,
        SplashScreen,
        { provide: ErrorHandler, useClass: IonicErrorHandler }
    ]
})
export class AppModule { }



